	import {mapState } from 'vuex';
import articleList from '@/components/articleList.vue';
export default {
	data() {
		return {
			type: -1,
			more: false,
			pageNum: 1,
			pageSize: 20,
			loading: true,
			list: [],
		};
	},
	computed:{...mapState(["city"])},
	components: { articleList },
	onShareAppMessage() {
		let token = uni.getStorageSync('TOKEN');
		let user = uni.getStorageSync('userInfo');
		if (token) {
			return {
				title: '易建联共享服务平台',
				path: '/pages/index/index?server=' + user.server_people_id
			};
		}
	},
	onLoad(opt) {
		console.log('路由参数：', opt);
		if (opt.type) {
			this.type = opt.type;
			uni.setNavigationBarTitle({
				title: opt.title
			});
			this.getList();
		}
	},
	onReachBottom() {
		this.getList();
	},
	methods: {
		getList() {
			console.log(this.city)
			if (this.loading) {
				this.loading = false;
				this.$Fetch({
					url: 'news/getData',
					data: {
						pageNum: this.pageNum,
						pageSize: this.pageSize,
						type: this.type,
						city: this.city
					},debug:true,
					success: res => {
						let arr=res.lists;
						if (arr.length > 0) {
							this.list = this.list.concat(arr);
							if (arr.length === this.pageSize) {
								this.pageNum++;
								this.loading = true;
								this.more = true;
							} else {
								this.more = false;
							}
						}else{
							this.more = false;
						}
					}
				});
			}
		}
	}
};
